.tableArea {
  margin: 10px 0 10px 10px;
  border: 1px solid #ddd;
  border-radius: 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column; }
  .tableArea table {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto; }
  .tableArea .pagination {
    -ms-flex: 0 0 auto;
        flex: 0 0 auto; }

.pagination {
  background: #f7f9fa;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row; }
  .pagination i {
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
    margin: 10px; }
  .pagination div {
    -ms-flex: 1 0 auto;
        flex: 1 0 auto;
    text-align: right; }

table {
  display: grid;
  /* This is a (hacky) way to make the .grid element size to fit its content */
  overflow: auto;
  width: 100%;
  border-spacing: 0;
  border-collapse: separate;
  background: white;
  font-size: 12px; }

thead {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: block;
  z-index: 20;
  border-bottom: 2px solid #ddd;
  color: #8091a5; }

th {
  -webkit-transition: background-color 200ms ease;
  -o-transition: background-color 200ms ease;
  transition: background-color 200ms ease;
  background: #f7f9fa; }

th, td {
  min-width: 300px;
  display: table-cell;
  text-align: left;
  border-right: 1px solid #eee; }

.stickyCol th:first-of-type, .stickyCol td:first-of-type {
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  z-index: 10;
  -webkit-transition: border-color 200ms ease;
  -o-transition: border-color 200ms ease;
  transition: border-color 200ms ease; }

table td:first-of-type, table.scrolled.stickyCol td:first-of-type {
  background: white; }

th:first-of-type, table.scrolled.stickyCol th:first-of-type {
  background: #f7f9fa; }

table.scrolled th:first-of-type, table.scrolled.stickyCol td:first-of-type {
  border-right: 1px solid #ddd; }

tr {
  display: block;
  border-bottom: 1px solid #e5ebed; }

td {
  -webkit-transition: background-color 200ms ease;
  -o-transition: background-color 200ms ease;
  transition: background-color 200ms ease;
  background-color: white; }

table.scrolled td {
  background-color: #fbfbfb; }

table.scrolled th {
  background-color: #f1f3f5; }

table tbody tr td {
  padding-top: 16px;
  padding-bottom: 16px; }

table th {
  padding-top: 8px;
  padding-bottom: 8px; }

th span, td span {
  margin: 0 10px;
  display: block; }



