/*Loader*/
.loader {
  height: 1px;
  width: 100%;
  position: relative;
  overflow: hidden;
  /*background-color: #ddd;*/ }

.loader:before {
  display: block;
  position: absolute;
  content: "";
  left: -200px;
  width: 200px;
  height: 1px;
  background-color: #2980b9;
  -webkit-animation: loading 1.5s linear infinite;
          animation: loading 1.5s linear infinite; }

@-webkit-keyframes loading {
  from {
    left: -200px;
    width: 30%; }
  50% {
    width: 30%; }
  70% {
    width: 70%; }
  80% {
    left: 50%; }
  95% {
    left: 120%; }
  to {
    left: 100%; } }

@keyframes loading {
  from {
    left: -200px;
    width: 30%; }
  50% {
    width: 30%; }
  70% {
    width: 70%; }
  80% {
    left: 50%; }
  95% {
